import { Component, OnInit } from '@angular/core';
import { ItemsService } from '../items.service';
import { Response } from '@angular/http';

@Component({
  selector: 'app-armenia-page',
  templateUrl: './armenia-page.component.html',
  styleUrls: ['./armenia-page.component.css']
})
export class ArmeniaPageComponent implements OnInit {

  armeniaItems = [];

  constructor(private itemsService: ItemsService) {}

  ngOnInit() {
    this.itemsService
    .getArmeniaItems()
    .subscribe((response: Response) => {
      const armaniaData = response.json();
      this.armeniaItems = armaniaData;
      console.log(armaniaData);
    });
  }
}



