import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

@Injectable()
export class ItemsService {
    constructor(private http: Http) {}

    getArmeniaItems() {
        return this.http.get('http://localhost:3000/ArmeniaPageItems');
    }

    getBlogItems() {
        return this.http.get('http://localhost:3000/BlogPageItems');
    }
}



